import React from "react";
import { Link } from "react-router-dom";
import { globalContext } from "../Context/Context";
import PropTypes from "prop-types";
import furniture from "../images/furniture.jpg";
class Cart extends React.Component {
  async componentDidMount() {
    await this.context.checkIfLogin();
    await this.context.getCartItemsFromDatabase();
  }
  render() {
    return (
      <div>
        <nav className="HomePageNav">
          <h3>{this.context.state.NavHeading}</h3>
          <div className="HomePageLinkCont">
            <Link to="/ShoppingApp">Shopping</Link>
            <button
              style={{ display: this.context.state.LoggedIn ? "" : "none" }}
              className="LogoutBtn"
              onClick={this.context.logoutClick}
            >
              Logout
            </button>
          </div>
        </nav>
        <ul className="container CartUl">
          {this.context.state.CartItems.map((ele) => {
            return (
              <li key={ele._id}>
                <img src={furniture} alt="Item"></img>
                <p>{ele.name}</p>
                <p>Qty: {ele.quantity}</p>
                <button
                  className="btn-danger"
                  onClick={(id) =>
                    this.context.removeItemFromCartClick(ele._id)
                  }
                >
                  X
                </button>
              </li>
            );
          })}
        </ul>
        <div className="container CartCheckoutDiv">
          <Link className="btn-primary" to="/Cart/Checkout">
            Checkout
          </Link>
          <Link className="btn-primary" to="/">
            Continue Shopping
          </Link>
        </div>
      </div>
    );
  }
}
Cart.contextType = globalContext;

// Proptypes
Cart.propTypes = {
  checkIfLogin: PropTypes.func,
  getCartItemsFromDatabase: PropTypes.func,
  NavHeading: PropTypes.string,
  LoggedIn: PropTypes.bool,
  logoutClick: PropTypes.func,
  CartItems: PropTypes.array,
  removeItemFromCartClick: PropTypes.func,
};
export default Cart;
