import React from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
export const globalContext = React.createContext();

class GlobalStateProvider extends React.Component {
  state = {
    LoginEmail: "",
    LoginPassword: "",
    LoginFailed: false,
    LoginFailedMsg: "",
    RegisterUsername: "",
    RegisterEmail: "",
    RegisterPassword: "",
    RegisterConfirmPassword: "",
    RegisterFailed: false,
    PasswordLength: false,
    PasswordCapitalLetter: false,
    PasswordSpecialChar: false,
    ConfirmPasswordTest: false,
    RegisterFailedText: "",
    RegisterFailedBorder: "1px solid Red",
    RegisterSuccess: false,
    GetShoppingItems: [],
    ShoppingItems: [],
    AddedToCart: false,
    LoggedIn: false,
    CheckedOutOrder: false,
    Loading: false,
    SearchItems: "",
    CartItems: [],
    NavHeading: "React Shopping App",
  };
  checkIfLogin = () => {
    let checkLogin = window.sessionStorage.getItem("token");
    if (checkLogin) {
      this.setState({
        ...this.state,
        LoggedIn: true,
      });
    } else {
      this.setState({
        ...this.state,
        LoggedIn: false,
      });
    }
  };
  getShoppingItems = () => {
    this.setState({
      ...this.state,
      Loading: true,
    });
    axios
      .get("https://aqueous-sierra-57355.herokuapp.com/Shopping")
      .then((response) => {
        this.setState({
          ...this.state,
          GetShoppingItems: response.data,
          ShoppingItems: response.data,
          Loading: false,
        });
      })
      .catch((err) => {
        console.log(err);
      });
    window.console.clear();
  };
  loginEmailChange = (e) => {
    this.setState({
      LoginEmail: e.target.value,
    });
  };
  loginPasswordChange = (e) => {
    this.setState({
      LoginPassword: e.target.value,
    });
  };
  loginSubmit = (e) => {
    e.preventDefault();
    axios
      .post("https://aqueous-sierra-57355.herokuapp.com/Login", {
        data: {
          Email: this.state.LoginEmail,
          Password: this.state.LoginPassword,
        },
      })
      .then((response) => {
        this.setState({
          ...this.state,
          LoggedIn: true,
          LoginEmail: "",
          LoginPassword: "",
          NavHeading: `Welcome ${response.data.Username}`,
        });
        window.sessionStorage.setItem(
          "token",
          JSON.stringify(response.data.token)
        );
        window.sessionStorage.setItem(
          "Items",
          JSON.stringify(response.data.Items)
        );
        window.console.clear();
        this.props.history.push("/ShoppingApp");
      })
      .catch((err) => {
        this.setState({
          ...this.state,
          LoginFailed: true,
          LoginFailedMsg: err.response.data.msg,
          LoginEmail: "",
          LoginPassword: "",
        });
      });
  };
  logoutClick = () => {
    let getToken = window.sessionStorage.getItem("token");
    let parseToken = JSON.parse(getToken);
    let getItems = window.sessionStorage.getItem("Items");
    let parseItems = JSON.parse(getItems);
    axios
      .post("https://aqueous-sierra-57355.herokuapp.com/SaveItems", {
        headers: { "x-auth-token": parseToken },
        data: {
          Items: parseItems,
        },
      })
      .then((response) => {
        if (response.status === 200) {
          this.setState({
            ...this.state,
            LoggedIn: false,
            NavHeading: "React Shopping App",
            CartItems: [],
          });
          window.sessionStorage.removeItem("token");
          window.sessionStorage.removeItem("Items");
          this.props.history.push("/ShoppingApp");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  registerUsernameChange = (e) => {
    this.setState({
      ...this.state,
      RegisterUsername: e.target.value,
    });
  };
  registerEmailChange = (e) => {
    this.setState({
      ...this.state,
      RegisterEmail: e.target.value,
    });
  };
  testPasswordRequirements = () => {
    // Checking if password is at least 8 characters long
    if (this.state.RegisterPassword.length >= 8) {
      this.setState({
        PasswordLength: true,
      });
    } else {
      this.setState({
        PasswordLength: false,
      });
    }

    // Checking if password has a capital letter
    let capRegex = /[A-Z]/;
    let capTest = capRegex.test(this.state.RegisterPassword);
    this.setState({
      PasswordCapitalLetter: capTest,
    });

    // Check if password has a special character
    let specCharacterRegex = /(?=.*[!@#$%^&*])/;
    let specialCharacterTest = specCharacterRegex.test(
      this.state.RegisterPassword
    );
    this.setState({
      PasswordSpecialChar: specialCharacterTest,
    });
  };
  registerPasswordChange = async (e) => {
    await this.setState({
      ...this.state,
      RegisterPassword: e.target.value,
    });
    await this.testPasswordRequirements();
  };
  registerConfirmPasswordChange = (e) => {
    this.setState({
      RegisterConfirmPassword: e.target.value,
    });
  };
  registerAccountSubmit = async (e) => {
    e.preventDefault();
    let passMatchTest =
      this.state.RegisterPassword === this.state.RegisterConfirmPassword
        ? true
        : false;
    if (this.state.PasswordLength === false) {
      this.setState({
        RegisterFailed: true,
        RegisterFailedText: "Password is not at least 8 characters long",
      });
    } else if (this.state.PasswordCapitalLetter === false) {
      this.setState({
        RegisterFailed: true,
        RegisterFailedText: "Password does not contain capital letter",
      });
    } else if (this.state.PasswordSpecialChar === false) {
      this.setState({
        RegisterFailed: true,
        RegisterFailedText:
          "Password does not contain special character ex.[@, #, %]",
      });
    } else if (passMatchTest === false) {
      this.setState({
        RegisterFailed: true,
        RegisterFailedText: "Passwords do not match",
      });
    } else {
      await axios
        .post("https://aqueous-sierra-57355.herokuapp.com/CreateAccount", {
          data: {
            username: this.state.RegisterUsername,
            email: this.state.RegisterEmail,
            password: this.state.RegisterPassword,
          },
        })
        .then((response) => {
          if (response.status === 200) {
            this.setState({
              ...this.state,
              RegisterUsername: "",
              RegisterEmail: "",
              RegisterPassword: "",
              RegisterConfirmPassword: "",
              RegisterFailed: false,
              PasswordLength: false,
              PasswordCapitalLetter: false,
              PasswordSpecialChar: false,
              ConfirmPasswordTest: false,
              RegisterFailedText: "",
              RegisterSuccess: true,
            });
          }
        })
        .catch((err) => {
          this.setState({
            ...this.state,
            RegisterFailed: true,
            RegisterFailedText: err.response.data.msg,
            RegisterUsername: "",
            RegisterEmail: "",
            RegisterPassword: "",
            RegisterConfirmPassword: "",
            PasswordLength: false,
            PasswordCapitalLetter: false,
            PasswordSpecialChar: false,
            ConfirmPasswordTest: false,
          });
        });
      setTimeout(() => {
        this.setState({
          ...this.state,
          RegisterSuccess: false,
          RegisterFailed: false,
          RegisterFailedText: "",
        });
        window.console.clear();
      }, 2500);
    }
  };
  registerFailedClick = () => {
    this.setState({
      ...this.state,
      RegisterFailed: false,
      RegisterFailedText: "",
    });
  };
  registerSuccessClick = () => {
    this.setState({
      ...this.state,
      RegisterSuccess: false,
    });
  };
  searchShoppingItems = (e) => {
    this.setState({
      ...this.state,
      SearchItems: e.target.value,
    });
  };
  searchShoppingItemsKeypress = (e) => {
    if (e.key === "Enter") {
      let filter = this.state.ShoppingItems.filter((ele) => {
        if (ele.name.toLowerCase() === this.state.SearchItems.toLowerCase()) {
          return ele;
        } else {
          return null;
        }
      });
      if (filter.length < 1) {
        this.setState({
          ...this.state,
          ShoppingItems: [...this.state.GetShoppingItems],
        });
      } else {
        this.setState({
          ...this.state,
          ShoppingItems: filter,
        });
      }
    }
  };
  searchShoppingItemsClick = () => {
    let filter = this.state.ShoppingItems.filter((ele) => {
      if (ele.name.toLowerCase() === this.state.SearchItems.toLowerCase()) {
        return ele;
      } else {
        return null;
      }
    });
    if (filter.length < 1) {
      this.setState({
        ...this.state,
        ShoppingItems: [...this.state.GetShoppingItems],
      });
    } else {
      this.setState({
        ...this.state,
        ShoppingItems: filter,
      });
    }
  };
  filterShoppingItemsSelect = (e) => {
    let filterType = e.target.value;
    if (filterType === "Name") {
      let nameFilter = this.state.ShoppingItems.sort((a, b) => {
        return a.name.localeCompare(b.name);
      });
      this.setState({
        ...this.state,
        ShoppingItems: nameFilter,
      });
    } else {
      let nameFilter = this.state.ShoppingItems.sort((a, b) => {
        return a.price - b.price;
      });
      this.setState({
        ...this.state,
        ShoppingItems: nameFilter,
      });
    }
  };
  addToCartClick = async (id) => {
    let getToken = window.sessionStorage.getItem("token");
    let getItems = window.sessionStorage.getItem("Items");
    let parseItems = JSON.parse(getItems);
    if (!getToken) {
      this.setState({
        ...this.state,
        LoginFailed: true,
        LoginFailedMsg: "You must login to add items to cart",
      });
      this.props.history.push("/Login");
    } else {
      let addItem = await this.state.ShoppingItems.find((ele) => {
        if (ele._id === id) {
          return ele;
        } else {
          return null;
        }
      });
      let checkItem = parseItems.some((ele) => {
        if (ele._id === addItem._id) {
          return true;
        } else {
          return false;
        }
      });
      if (checkItem) {
        this.setState({
          ...this.state,
          AddedToCart: true,
        });
        let updatedItems = await parseItems.map((ele) => {
          if (ele._id === addItem._id) {
            ele.quantity += 1;
          }
          return ele;
        });
        window.sessionStorage.setItem("Items", JSON.stringify(updatedItems));
      } else {
        this.setState({
          ...this.state,
          AddedToCart: true,
        });
        parseItems.push(addItem);
        window.sessionStorage.setItem("Items", JSON.stringify(parseItems));
      }
    }

    setTimeout(() => {
      this.setState({
        ...this.state,
        AddedToCart: false,
      });
    }, 1000);
  };
  removeItemFromCartClick = async (id) => {
    let getToken = window.sessionStorage.getItem("token");
    let getItems = window.sessionStorage.getItem("Items");
    let parseItems = JSON.parse(getItems);
    if (!getToken) {
      this.setState({
        ...this.state,
        LoginFailed: true,
        LoginFailedMsg: "You must login to add items to cart",
      });
      this.props.history.push("/Login");
    }
    let updatedItems = await parseItems.filter((ele) => {
      if (ele._id !== id) {
        return ele;
      } else {
        return null;
      }
    });
    this.setState({
      ...this.state,
      CartItems: updatedItems,
    });
    window.sessionStorage.setItem("Items", JSON.stringify(updatedItems));
  };
  checkoutClick = () => {
    let getToken = window.sessionStorage.getItem("token");
    let parseToken = JSON.parse(getToken);
    this.setState({
      ...this.state,
      CheckedOutOrder: true,
    });
    axios
      .delete("https://aqueous-sierra-57355.herokuapp.com/Cart/Delete", {
        headers: { "x-auth-token": parseToken },
      })
      .then((response) => {
        window.sessionStorage.removeItem("Items");
        window.sessionStorage.setItem(
          "Items",
          JSON.stringify(response.data.Items)
        );
        setTimeout(() => {
          this.setState({
            ...this.state,
            CheckedOutOrder: false,
          });
          this.props.history.push("/ShoppingApp");
        }, 1200);
      })
      .catch((err) => {
        this.setState({
          ...this.state,
          LoginFailed: true,
          LoginFailedMsg: err.response.data.msg,
        });
        this.props.history.push("/Login");
      });
  };
  closeLoginFailedWarningClick = () => {
    this.setState({
      ...this.state,
      LoginFailed: false,
      LoginFailedMsg: "",
    });
  };
  getCartItemsFromDatabase = () => {
    let getToken = window.sessionStorage.getItem("token");
    if (!getToken) {
      this.setState({
        ...this.state,
        LoginFailed: true,
        LoginFailedMsg: "You must login to see your cart",
      });
      this.props.history.push("/Login");
    } else {
      let getItems = window.sessionStorage.getItem("Items");
      let parseItems = JSON.parse(getItems);
      this.setState({
        ...this.state,
        CartItems: [...parseItems],
      });
    }
  };

  render() {
    return (
      <globalContext.Provider
        value={{
          state: this.state,
          loginEmailChange: this.loginEmailChange,
          loginPasswordChange: this.loginPasswordChange,
          loginSubmit: this.loginSubmit,
          registerUsernameChange: this.registerUsernameChange,
          registerEmailChange: this.registerEmailChange,
          registerPasswordChange: this.registerPasswordChange,
          registerConfirmPasswordChange: this.registerConfirmPasswordChange,
          registerFailedClick: this.registerFailedClick,
          registerAccountSubmit: this.registerAccountSubmit,
          searchShoppingItems: this.searchShoppingItems,
          searchShoppingItemsClick: this.searchShoppingItemsClick,
          searchShoppingItemsKeypress: this.searchShoppingItemsKeypress,
          filterShoppingItemsSelect: this.filterShoppingItemsSelect,
          addToCartClick: this.addToCartClick,
          removeItemFromCartClick: this.removeItemFromCartClick,
          logoutClick: this.logoutClick,
          checkoutClick: this.checkoutClick,
          getShoppingItems: this.getShoppingItems,
          closeLoginFailedWarningClick: this.closeLoginFailedWarningClick,
          checkIfLogin: this.checkIfLogin,
          getCartItemsFromDatabase: this.getCartItemsFromDatabase,
        }}
      >
        {this.props.children}
      </globalContext.Provider>
    );
  }
}

export default withRouter(GlobalStateProvider);
