import React from "react";
import { Link } from "react-router-dom";
import { globalContext } from "../Context/Context";
import PropTypes from "prop-types";
class Login extends React.Component {
  componentDidMount() {
    this.context.checkIfLogin();
  }
  render() {
    return (
      <div>
        <nav className="HomePageNav">
          <h3>{this.context.state.NavHeading}</h3>
          <div className="HomePageLinkCont">
            <Link to="/ShoppingApp">Shopping</Link>
            <Link to="/CreateAccount">Create Account</Link>
            <Link
              style={{ display: this.context.state.LoggedIn ? "" : "none" }}
              to="/Cart"
            >
              Cart
            </Link>
            <button
              style={{ display: this.context.state.LoggedIn ? "" : "none" }}
              className="LogoutBtn"
              onClick={this.context.logoutClick}
            >
              Logout
            </button>
          </div>
        </nav>
        <form
          onSubmit={(e) => {
            this.context.loginSubmit(e);
          }}
          className="container LoginForm"
        >
          <div className="LoginEmailDiv">
            <label htmlFor="Email">Email</label>
            <input
              type="Email"
              required
              placeholder="Enter Email"
              value={this.context.state.LoginEmail}
              onChange={(e) => {
                this.context.loginEmailChange(e);
              }}
              name="LoginEmail"
            ></input>
          </div>
          <div className="LoginPasswordDiv">
            <label htmlFor="Password">Password</label>
            <input
              type="Password"
              required
              placeholder="Enter Password"
              value={this.context.state.LoginPassword}
              onChange={(e) => {
                this.context.loginPasswordChange(e);
              }}
              name="LoginPassword"
            ></input>
          </div>
          <button type="Submit" className="btn-primary LoginButton">
            Login
          </button>
        </form>
        <div
          style={{ display: this.context.state.LoginFailed ? "" : "none" }}
          className="container LoginWarning alert alert-danger LoginFailedAlert"
        >
          {this.context.state.LoginFailedMsg}
          <button
            onClick={this.context.closeLoginFailedWarningClick}
            style={{
              position: "absolute",
              right: "0",
              marginRight: "20px",
            }}
            className="btn-close "
          ></button>
        </div>
      </div>
    );
  }
}
Login.contextType = globalContext;

// PropTypes
Login.propTypes = {
  NavHeading: PropTypes.string,
  LoggedIn: PropTypes.bool,
  logoutClick: PropTypes.func,
  loginSubmit: PropTypes.func,
  LoginEmail: PropTypes.string,
  loginEmailChange: PropTypes.func,
  LoginPassword: PropTypes.string,
  loginPasswordChange: PropTypes.func,
  LoginFailed: PropTypes.bool,
  LoginFailedMsg: PropTypes.string,
  closeLoginFailedWarningClick: PropTypes.func,
};
export default Login;
