import React from 'react';
import {Link} from 'react-router-dom';
import {globalContext} from '../Context/Context';
import refresh from '../images/refresh.svg'
import Proptypes from 'prop-types';
import furniture from '../images/furniture.jpg'
class Home extends React.Component {
    async componentDidMount(){
        await this.context.getShoppingItems();
        await this.context.checkIfLogin();
    }
    render() { 
        return ( 
        <>
                <div className='LoadingContDiv' style={{display:this.context.state.Loading ? '' : 'none'}}>
                    <img  className='LoadingImage' src={refresh} alt='Loading'></img>
                </div>
            <div style={{display:this.context.state.Loading ? 'none' : ''}}>
                <nav className='HomePageNav'>
                    <h3>{this.context.state.NavHeading}</h3>
                        <div className='HomePageLinkCont'>
                            <Link style={{display:this.context.state.LoggedIn ? 'none' : ''}} to='/Login'>Login</Link>
                            <Link to='/CreateAccount'>Create Account</Link>
                            <Link  style={{display:this.context.state.LoggedIn ? '' : 'none'}}  to='/Cart'>Cart</Link>
                            <button 
                            style={{display:this.context.state.LoggedIn ? '' : 'none'}} 
                            className='LogoutBtn'
                            onClick={this.context.logoutClick}
                            >Logout</button>
                        </div>
                </nav>
                <div className=' ShoppingPageFilterDiv'>
                    <input 
                    type='text' 
                    value={this.context.state.SearchItems} 
                    placeholder='Search'
                    onChange={(e) => {this.context.searchShoppingItems(e)}}
                    onKeyPress={(e) => {this.context.searchShoppingItemsKeypress(e)}}
                    ></input>
                    <button 
                    className='btn-primary'
                    onClick={() => {this.context.searchShoppingItemsClick()}}
                    >Search</button>
                    <select
                    onChange={(e) => {this.context.filterShoppingItemsSelect(e)}}
                    defaultValue='Filter'>
                        <option  disabled hidden>Filter</option>
                        <option value='Name'>Name</option>
                        <option value='Price'>Price</option>
                    </select>
                </div>
                <div>
                        <div style={{display:this.context.state.AddedToCart ? '' : 'none'}} className="container AddedToCart alert alert-success" >
                        Item Added to Cart
                    </div>
                    <ul className='container ShoppingPageUl'>
                        {this.context.state.ShoppingItems.map((ele) => {
                            return(
                            <li key={ele._id}>
                            <img src={furniture} alt='Shopping Item'></img>
                            <h5>{ele.name}</h5>
                            <p>{ele.description}</p>
                            <p>${(Math.round(ele.price * 100) / 100).toFixed(2)}</p>
                            <button
                            className='btn-primary'
                            onClick={(id) => {this.context.addToCartClick(ele._id)}}
                            >Add to Cart</button>
                        </li>
                            )
                        })}
                    </ul>

                </div>
            </div>
        </>
         );
    }
}
Home.contextType = globalContext;

// PropTypes
Home.propTypes = {
    Loading: Proptypes.bool,
    NavHeading:Proptypes.string,
    LoggedIn:Proptypes.bool,
    logoutClick:Proptypes.func,
    SearchItems:Proptypes.string,
    searchShoppingItems:Proptypes.func,
    searchShoppingItemsKeypress:Proptypes.func,
    searchShoppingItemsClick:Proptypes.func,
    filterShoppingItemsSelect:Proptypes.func,
    AddedToCart:Proptypes.bool,
    ShoppingItems:Proptypes.array,
    addToCartClick:Proptypes.func
};
export default Home;